<template>
  <v-container>
    <v-row class="text-center">
      <v-col col="12" md="8">
        <v-text-field
          v-model="idDispostivo"
          :counter="max"
          :rules="rules"
          label="Identificação do Dispositivo"
        >
        </v-text-field>
      </v-col>
      <v-col col="12" md="2">
        <v-btn @click="getMedidas" rounded color="primary" dark>Filtrar</v-btn>
      </v-col>

      <v-col col="12" md="2">
        <v-btn :click="deleteall" rounded color="error" dark>Excluir tudo</v-btn>
      </v-col>
    </v-row>
    <v-row class="text-center">
      <v-col sm="12">
        <template>
          <v-data-table :headers="headers" :items="itens" class="elevation-1">
            <template v-slot:item.status="{ item }">
              <v-chip :color="getColor(item.status)" dark>{{ item.status }}</v-chip>
            </template>
          </v-data-table>
        </template>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      idDispostivo: '',
      itens: [],
      headers: [
        {
          text: 'Data Hora',
          align: 'start',
          sortable: false,
          value: 'dt_read',
        },
        { text: 'Dispositivo', value: 'deviceid' },
        { text: 'Status', value: 'status' },
        { text: 'L01', value: 'sread1' },
        { text: 'L02', value: 'sread2' },
        { text: 'L03', value: 'sread3' },
        { text: 'L04', value: 'sread4' },
        { text: 'L05', value: 'sread5' },
        { text: 'L06', value: 'sread6' },
        { text: 'L07', value: 'sread7' },
        { text: 'L08', value: 'sread8' },
        { text: 'S01', value: 'sstatus1' },
        { text: 'S02', value: 'sstatus1' },
        { text: 'S03', value: 'sstatus1' },
        { text: 'S04', value: 'sstatus1' },
        { text: 'H01', value: 'sable1' },
        { text: 'H02', value: 'sable2' },
        { text: 'H03', value: 'sable3' },
        { text: 'H04', value: 'sable4' },
      ],
    };
  },
  methods: {
    getColor() {
      return 'green';
    },
    async deleteall() {
      const response = await axios({
        method: 'post',
        url: 'http://192.168.1.75:3000/v1/log/delete',
        data: {
          deviceid: this.idDispostivo || 'ALL',
        },
      });
      alert(response.data);
    },
    async getMedidas() {
      let murl = 'http://192.168.1.75:3000/v1/log/leituras';
      if (this.idDispostivo === '') {
        murl += '/ALL';
      } else {
        murl += `/${this.idDispostivo.toString()}`;
      }
      const response = await axios({
        method: 'get',
        url: murl,
      });
      this.itens = response.data.retorno;
    },
    async onLoad() {
      await this.getMedidas();
    },

    async checkLogin() {
      const loggedIn = localStorage.getItem('user');
      if (!loggedIn) {
        this.$router.push('/login');
      }
    },
  },
  created() {
    this.checkLogin();
    this.onLoad();
  },
};
</script>
